import { Divider, Stack, Typography, useTheme } from "@mui/material";

import { Strings } from "@app/constants";
import { Icon } from "@iconify/react";

const FooterBottom = () => {
  const theme = useTheme();

  return (
    <>
      <Divider
        sx={{
          width: "100%",
          backgroundColor: theme.palette.common.white,
        }}
      />
      <Stack
        sx={{
          width: "100%",
          flexDirection: { xs: "column", sm: "row" },
          justifyContent: "space-between",
          alignItems: "center",
          gap: 2,
        }}
      >
        <Typography
          variant="body2"
          color="white"
          textAlign="center"
          sx={{
            fontWeight: "600",
            color: theme.palette.common.white,
            textAlign: "center",
            alignSelf: "center",
          }}
          fontSize={"14"}
        >
          {Strings.footer.allRights}
        </Typography>
        <Typography
          variant="body1"
          color="white"
          textAlign="center"
          sx={{
            fontWeight: "600",
            color: theme.palette.common.white,
            textAlign: "center",
            alignSelf: "center",
            alignItems: "center",
            cursor: "pointer",
          }}
          fontSize={"14"}
          onClick={() =>
            window.open("https://www.instagram.com/yashwant_kargwal/", "_blank")
          }
        >
          Built by <Icon icon={"line-md:instagram"} /> Yashwant Kargwal
        </Typography>
      </Stack>
    </>
  );
};

export { FooterBottom };
