import { IconButton, Stack, Typography, useTheme } from "@mui/material";

import { imageSource, Strings } from "@app/constants";
import { Icon } from "@iconify/react";
import { socialMediaLinks } from "@app/utils/socialMediaLinks";

const FooterBrand = () => {
  const theme = useTheme();

  return (
    <Stack width={{ xs: "100%", sm: "70%" }} alignSelf={"center"}>
      <Stack direction="row" gap={1} alignItems="center" pt={8}>
        <img
          src={imageSource.logo}
          alt="logo"
          style={{ width: 30, height: 30 }}
        />
        <Typography
          variant="h6"
          fontWeight="600"
          color={theme.palette.common.white}
          fontSize="16"
        >
          {Strings.footer.gedarTrader}
        </Typography>
      </Stack>

      <Typography
        variant="body2"
        sx={{ mt: 1, width: { xs: "100%", sm: "53%" } }}
        fontWeight="500"
        color={theme.palette.common.white}
        fontSize="14"
      >
        {Strings.footer.trustedWeldingShop}
      </Typography>

      <Stack direction="row" sx={{ mt: 2 }}>
        <IconButton
          onClick={() => window.open(socialMediaLinks.instagram, "_blank")}
        >
          <Icon icon={"skill-icons:instagram"} />
        </IconButton>
        <IconButton
          onClick={() => window.open(socialMediaLinks.facebook, "_blank")}
        >
          <Icon icon={"logos:facebook"} />
        </IconButton>
        <IconButton
          onClick={() => window.open(socialMediaLinks.youtube, "_blank")}
        >
          <Icon icon={"openmoji:youtube"} />
        </IconButton>
      </Stack>
    </Stack>
  );
};

export { FooterBrand };
