import React from "react";
import { Box, Paper, Typography, Stack, useTheme } from "@mui/material";
import { Strings } from "@app/constants";

interface Testimonial {
  name: string;
  message: string;
}

const testimonials: Testimonial[] = [
  {
    name: "Ramesh Kumar",
    message:
      "Gedar Traders has been my go-to shop for welding tools since years. Reliable and trustworthy!",
  },
  {
    name: "Sandeep Sharma",
    message:
      "Excellent quality and great customer service. They truly care about their customers.",
  },
  {
    name: "Anita Verma",
    message:
      "I always recommend Gedar Traders to my colleagues. Their products are durable and reasonably priced.",
  },
];

const TestimonialsCarousel = () => {
  const theme = useTheme();
  return (
    <Stack
      sx={{
        backgroundColor: theme.palette.primary.main,
      }}
    >
      <Typography
        variant="h3"
        sx={{
          textAlign: "center",
          mt: 3,
          color: theme.palette.common.white,
          textDecoration: `1px underline solid ${theme.palette.common.white}`,
          textUnderlineOffset: 3,
        }}
      >
        {Strings.testimonial.whatOurClientSay}
      </Typography>
      <Box sx={{ width: "100%", maxWidth: 1000, mx: "auto", py: 6 }}>
        <Stack
          direction={{ xs: "column", sm: "row" }}
          spacing={3}
          justifyContent="center"
          alignItems="stretch"
        >
          {testimonials.map((t, i) => (
            <Paper
              key={i}
              elevation={6}
              sx={{
                p: 4,
                flex: 1,
                borderRadius: 3,
                textAlign: "center",
                minHeight: 180,
                display: "flex",
                flexDirection: "column",
                justifyContent: "center",
                alignItems: "center",
                background: (theme) =>
                  theme.palette.mode === "dark"
                    ? "rgba(30,41,59,0.85)"
                    : "rgba(255,255,255,0.9)",
                boxShadow: `0 8px 24px rgba(0,0,0,0.1)`,
                transition: "transform 200ms ease, box-shadow 200ms ease",
                "&:hover": {
                  transform: "translateY(-6px)",
                  boxShadow: `0 18px 40px rgba(15,23,42,0.14)`,
                },
              }}
            >
              <Typography variant="body1" gutterBottom>
                "{t.message}"
              </Typography>
              <Typography variant="subtitle2" color="text.secondary">
                – {t.name}
              </Typography>
            </Paper>
          ))}
        </Stack>
      </Box>
    </Stack>
  );
};

export { TestimonialsCarousel };
