// src/sections/home/ProductSlider.tsx
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/autoplay";
import { Autoplay } from "swiper/modules";
import { Stack, Typography, useTheme } from "@mui/material";
import { ProductCard } from "@app/components";
import { useNavigate } from "react-router-dom";
import { PATH_DASHBOARD } from "@app/routes/paths";
import { useEffect } from "react";
import SwiperCore from "swiper";

interface ProductSliderProps {
  data: any[];
  slidesToShow?: number;
}

const ProductSlider = ({ data, slidesToShow = 4 }: ProductSliderProps) => {
  const theme = useTheme();
  const navigate = useNavigate();
  const products = data?.slice(0, 10) || [];

  // 👇 Swiper ko manually resize karne ke liye fix (important for MUI + responsive)
  useEffect(() => {
    setTimeout(() => {
      SwiperCore.update();
    }, 500);
  }, [slidesToShow]);

  return (
    <Stack
      sx={{
        width: "100%",
        py: 5,
        overflow: "hidden", // 👈 overflow fix
      }}
    >
      <Typography
        variant="h4"
        textAlign="center"
        gutterBottom
        sx={{
          fontWeight: 700,
          textDecoration: "underline 2px solid",
          textDecorationColor: theme.palette.primary.main,
          textUnderlineOffset: "6px",
          mb: 3,
        }}
      >
        Featured Products
      </Typography>

      <Swiper
        modules={[Autoplay]}
        slidesPerView={slidesToShow}
        spaceBetween={20}
        loop={true}
        centeredSlides={false}
        autoplay={{
          delay: 2500,
          disableOnInteraction: false, // 👈 autoplay continues after swipe
        }}
        speed={1200}
        grabCursor={true}
        style={{
          width: "100%",
          height: 420,
          boxSizing: "border-box",
        }}
        breakpoints={{
          0: { slidesPerView: 1 },
          600: { slidesPerView: 2 },
          960: { slidesPerView: 3 },
          1280: { slidesPerView: slidesToShow },
        }}
      >
        {products.map((item: any) => (
          <SwiperSlide
            key={item.id}
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <Stack
              sx={{
                px: 1,
                alignSelf: "stretch",
                width: "100%",
                maxWidth: 350,
              }}
            >
              <ProductCard
                id={item.id}
                isPrice={false}
                name={item.name}
                category={item.category}
                description={item.description}
                image={item.image}
                price={item.price}
                onViewMore={() =>
                  navigate(`${PATH_DASHBOARD.productDetail}/${item.id}`)
                }
              />
            </Stack>
          </SwiperSlide>
        ))}
      </Swiper>
    </Stack>
  );
};

export { ProductSlider };
