import React, { forwardRef } from "react";
import { Stack, useTheme } from "@mui/material";
import { useForm } from "react-hook-form";

import { ContactUsForm } from "./contact-us-form";
import { FooterBrand } from "./footer-brands";
import { FooterLinks } from "./footer-links";
import { FooterBottom } from "./footer-bottom";
import { GedarTrader } from "@app/constants/gedarTrader";

const Footer = forwardRef<HTMLDivElement>((props, ref) => {
  const theme = useTheme();

  const methods = useForm({
    defaultValues: { name: "", phone: "", message: "" },
  });

  const onSubmit = (form: any) => {
    const phoneNumber = GedarTrader.phoneNumber;

    const message = `Name: ${form.name}\nPhone: ${form.phone}\nMessage: ${form.message}`;

    const url = `https://wa.me/${phoneNumber}?text=${encodeURIComponent(
      message
    )}`;

    window.open(url, "_blank");
  };

  return (
    <Stack ref={ref} alignItems="center">
      <ContactUsForm methods={methods} onSubmit={onSubmit} />
      <Stack
        sx={{
          flexDirection: "row",
          width: "100%",
          color: theme.palette.common.white,
          backgroundColor: theme.palette.primary.main,
          flexWrap: "wrap",
          justifyContent: "space-between",
          alignItems: "center",
          px: 4,
          // py: 2,
          rowGap: 4,
          alignSelf: "center",
        }}
      >
        <FooterBrand />
        <FooterLinks />
      </Stack>
      <Stack
        sx={{
          width: "100%",
          color: theme.palette.common.white,
          backgroundColor: theme.palette.primary.main,
          flexWrap: "wrap",
          justifyContent: "space-between",
          alignItems: "center",
          px: 4,
          py: 2,
          rowGap: 4,
          alignSelf: "center",
        }}
      >
        <FooterBottom />
      </Stack>
    </Stack>
  );
});

export { Footer };
