import { useRoutes } from "react-router-dom";

import { PATH_DASHBOARD } from "./paths";
import { Home } from "@app/modules/home";
import { Page404 } from "@app/modules/page404";
import Layout from "./layout";
import { PrivacyPolicy } from "@app/modules/privacyPolicy";
import { TermAndCondition } from "@app/modules/termAndConditions";
import { Service } from "@app/modules/service";
import { Product } from "@app/modules/products";
import { ProductDetail } from "@app/modules/product-detail";
import { AboutUs } from "@app/modules/aboutUs";

// ----------------------------------------------------------------------

export function Router() {
  return useRoutes([
    {
      path: "/",
      element: <Layout />,
      children: [
        { path: "/", element: <Home /> },
        { path: PATH_DASHBOARD.home, element: <Home /> },
        { path: PATH_DASHBOARD.products, element: <Product /> },
        { path: PATH_DASHBOARD.aboutUs, element: <AboutUs /> },
        { path: PATH_DASHBOARD.service, element: <Service /> },
        {
          path: `${PATH_DASHBOARD.productDetail}/:id`,
          element: <ProductDetail />,
        },
        { path: PATH_DASHBOARD.privacyPolicy, element: <PrivacyPolicy /> },
        {
          path: PATH_DASHBOARD.termAndCondition,
          element: <TermAndCondition />,
        },
      ],
    },
    { path: "*", element: <Page404 /> },
  ]);
}
