import { alpha } from "@mui/material/styles";

export type ColorSchema =
  | "primary"
  | "secondary"
  | "info"
  | "success"
  | "warning"
  | "error"
  | "quaternary"
  | "taskboard";

declare module "@mui/material/styles/createPalette" {
  interface TypeBackground {
    neutral: string;
  }
  interface SimplePaletteColorOptions {
    lighter: string;
    darker: string;
  }
  interface PaletteColor {
    lighter: string;
    darker: string;
  }
  interface Palette {
    quaternary: {
      main: string;
      light: string;
      lighter: string;
      darker: string;
      premiumDarker: string;
      dark: string;
      blue: string;
      darkBlue: string;
      lightGreen: string;
      darkGreen: string;
      success: string;
      customTurquoise: string;
      dodgerBlue: string;
      bottleGreen: string;
      navyBlue: string;
      lightBlue: string;
      ceruleanBlue: string;
      royalBlue: string;
      lightPink: string;
      lightPurple: string;
      lightOrange: string;
      lightParrot: string;
      lightYellow: string;
      aliceBlue: string;
      royalNavyBlue: string;
      lavender: string;
      orange: string;
      brown: string;
    };

    tertiary: {
      light: string;
      lighter: string;
      main: string;
    };
    chart: {
      skyBlue: string;
      tealGreen: string;
      sandyYellow: string;
      mintGreen: string;
      coralOrange: string;
    };
  }
}

