import { Stack, Typography, Button, Box, CardMedia, Grid } from "@mui/material";
import { useParams, useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";
import { Loading } from "@app/components";
import ErrorOutlineIcon from "@mui/icons-material/ErrorOutline";

interface Product {
  id: number;
  name: string;
  description: string;
  image: string[];
  video?: string;
  price: number;
  category: string;
}

const ProductDetail = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const [product, setProduct] = useState<Product | null>(null);
  const [loading, setLoading] = useState(true);
  const [selectedImage, setSelectedImage] = useState<string | null>(null);

  // 🔹 Add new state for active media type
  const [selectedType, setSelectedType] = useState<"image" | "video">("image");

  useEffect(() => {
    const fetchProduct = async () => {
      try {
        const res = await fetch(
          "https://raw.githubusercontent.com/gedartrader/Gedar-trader-web-server-json/refs/heads/main/products.json"
        );
        const jsonData = await res.json();

        const prod = jsonData.products.find(
          (p: Product) => p.id.toString() === id
        );

        if (prod) {
          setProduct(prod);
          if (prod.image?.length > 0) setSelectedImage(prod.image[0]);
        } else {
          setProduct(null);
        }
      } catch (e) {
        console.error(e);
      } finally {
        setLoading(false);
      }
    };

    fetchProduct();
  }, [id]);

  if (loading) return <Loading />;

  if (!product)
    return (
      <Box
        display="flex"
        flexDirection="column"
        justifyContent="center"
        alignItems="center"
        height="60vh"
        textAlign="center"
        gap={2}
        bgcolor="#f9f9f9"
        p={4}
        borderRadius={2}
        boxShadow={3}
      >
        <ErrorOutlineIcon sx={{ fontSize: 60, color: "#f50057" }} />
        <Typography variant="h5" fontWeight="bold">
          Oops! No Product Found
        </Typography>
        <Typography variant="body1" color="text.secondary">
          Sorry, we couldn't find this product.
        </Typography>
        <Button
          variant="contained"
          color="primary"
          onClick={() => navigate("/")}
        >
          Go to Home
        </Button>
      </Box>
    );

  // ---------- Product UI ----------
  return (
    <Stack spacing={3} alignItems="center" sx={{ p: 3 }}>
      {/* Title */}
      <Typography variant="h4" fontWeight="bold" textAlign="center">
        {product.name}
      </Typography>

      {/* 🔹 Main Media Box (Image or Video) */}
      <Box
        sx={{
          width: "100%",
          maxWidth: 500,
          height: 320,
          borderRadius: 3,
          overflow: "hidden",
          boxShadow: 2,
        }}
      >
        {selectedType === "image" && selectedImage ? (
          <CardMedia
            component="img"
            src={selectedImage}
            alt={product.name}
            sx={{ width: "100%", height: "100%", objectFit: "contain" }}
          />
        ) : (
          product.video && (
            <iframe
              width="100%"
              height="100%"
              src={`${convertYouTubeUrl(product.video)}?autoplay=1`}
              title="Product Video"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              allowFullScreen
            ></iframe>
          )
        )}
      </Box>

      {/* 🔹 Thumbnails + Video Icon */}
      <Grid
        container
        spacing={1}
        justifyContent="center"
        sx={{ maxWidth: 500 }}
      >
        {product.image.map((img, index) => (
          <Box key={index}>
            <CardMedia
              component="img"
              src={img}
              alt={`thumb-${index}`}
              onClick={() => {
                setSelectedImage(img);
                setSelectedType("image");
              }}
              sx={{
                width: 70,
                height: 70,
                objectFit: "contain",
                cursor: "pointer",
                borderRadius: 1,
                border:
                  selectedType === "image" && selectedImage === img
                    ? "2px solid #1976d2"
                    : "1px solid #ccc",
                transition: "all 0.2s ease",
                "&:hover": { transform: "scale(1.05)" },
              }}
            />
          </Box>
        ))}

        {/* 🔹 Add Video Thumbnail (if available) */}
        {product.video && (
          <Box
            onClick={() => setSelectedType("video")}
            sx={{
              width: 70,
              height: 70,
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              borderRadius: 1,
              border:
                selectedType === "video"
                  ? "2px solid #1976d2"
                  : "1px solid #ccc",
              cursor: "pointer",
              backgroundColor: "#000",
              color: "#fff",
              fontSize: 12,
              fontWeight: "bold",
              "&:hover": { transform: "scale(1.05)" },
            }}
          >
            ▶️ Video
          </Box>
        )}
      </Grid>

      {/* Description */}
      <Typography
        variant="body1"
        sx={{ maxWidth: 600, textAlign: "center", lineHeight: 1.6 }}
      >
        {product.description}
      </Typography>

      {/* Price */}
      <Typography variant="h5" fontWeight="bold" color="primary">
        ₹{product.price.toLocaleString("en-IN")}
      </Typography>

      {/* Category */}
      <Typography variant="subtitle2" color="text.secondary">
        Category: {product.category}
      </Typography>

      {/* Buttons */}
      <Stack direction="row" spacing={2}>
        <Button
          variant="contained"
          color="primary"
          onClick={() =>
            window.open(
              `https://wa.me/918619386297?text=Hi%20Gedar%20Trader,%20I%20am%20interested%20in%20your%20product:%20${encodeURIComponent(
                product.name
              )}`,
              "_blank"
            )
          }
        >
          Contact on WhatsApp
        </Button>
        <Button variant="outlined" onClick={() => navigate(-1)}>
          Back
        </Button>
      </Stack>
    </Stack>
  );
};

// 🔹 Convert YouTube URL to embeddable format
function convertYouTubeUrl(url: string): string {
  if (url.includes("youtube.com/shorts/")) {
    const videoId = url.split("shorts/")[1].split("?")[0];
    return `https://www.youtube.com/embed/${videoId}`;
  } else if (url.includes("watch?v=")) {
    const videoId = url.split("v=")[1].split("&")[0];
    return `https://www.youtube.com/embed/${videoId}`;
  }
  return url;
}

export { ProductDetail };
