import { useState } from "react";
import { useNavigate } from "react-router-dom";
import {
  Button,
  Drawer,
  IconButton,
  Stack,
  Typography,
  useTheme,
} from "@mui/material";
import { Icon } from "@iconify/react";
import Inventory2RoundedIcon from "@mui/icons-material/Inventory2Rounded";
import BuildIcon from "@mui/icons-material/Build";

import { imageSource, Strings } from "@app/constants";
import { NavbarLinks } from "./nav-bar-links";
import { PATH_DASHBOARD } from "@app/routes/paths";
import { useResponsive } from "@app/hooks";
import { redirectToWhatsApp } from "@app/utils/whatsapp";

const NavBar = () => {
  const theme = useTheme();
  const navigate = useNavigate();
  const isMobile = useResponsive("down", "sm");

  const [open, setOpen] = useState(false);

  const toggleDrawer = () => {
    setOpen(!open);
  };

  const defaultLinks: { label: string; onClick: () => void; path: string }[] = [
    {
      label: Strings.navBar.home,
      onClick: () => navigate(PATH_DASHBOARD.home),
      path: PATH_DASHBOARD.home,
    },
    {
      label: Strings.navBar.products,
      onClick: () => navigate(PATH_DASHBOARD.products),
      path: PATH_DASHBOARD.products,
    },
    {
      label: Strings.navBar.service,
      onClick: () => navigate(PATH_DASHBOARD.service),
      path: PATH_DASHBOARD.service,
    },
    {
      label: Strings.navBar.aboutUs,
      onClick: () => navigate(PATH_DASHBOARD.aboutUs),
      path: PATH_DASHBOARD.aboutUs,
    },
  ];

  return (
    <Stack
      sx={{
        height: { xs: 50, sm: 70 },
        width: "95%",
        alignSelf: "center",
        backgroundColor: "#ff99004c",
        flexDirection: "row",
        px: { xs: 1, sm: 2 },
        mt: "8px",
        alignItems: "center",
        justifyContent: "space-between",
        position: "fixed",
        zIndex: 1000,
        borderRadius: 1,
        backdropFilter: "blur(20px)",
        boxShadow: `0 0 0 1px ${theme.palette.primary.dark}`,
      }}
    >
      <Stack
        sx={{
          position: "absolute",
          bottom: -50,
          flexDirection: "row",
          gap: 4,
        }}
      >
        <Button
          variant="contained"
          startIcon={<Inventory2RoundedIcon />}
          onClick={() => navigate(PATH_DASHBOARD.products)}
        >
          {Strings.navBar.products}
        </Button>
        <Button
          variant="contained"
          startIcon={<BuildIcon />}
          onClick={() => navigate(PATH_DASHBOARD.service)}
        >
          {Strings.navBar.service}
        </Button>
      </Stack>
      <Stack
        sx={{
          // width: { xs: 40, sm: 50 },
          minWidth: 40,
          borderRadius: "10%",
          // overflow: "hidden",
          cursor: "pointer",
          flexDirection: "row",
          alignItems: "center",
          justifyContent: "center",
          gap: 1,
        }}
        onClick={() => navigate(PATH_DASHBOARD.home)}
      >
        <img
          src={imageSource.logo}
          style={{
            width: 40,
            aspectRatio: "1/1",
            borderRadius: 10,
          }}
        />
        <Typography sx={{ fontWeight: "bold" }}>Gedar Trader</Typography>
      </Stack>
      {!isMobile && (
        <Stack>
          <NavbarLinks
            navLinks={defaultLinks?.map((link) => ({
              label: link?.label,
              onClick: link?.onClick,
              path: link?.path,
            }))}
          />
        </Stack>
      )}
      {!isMobile && (
        <Stack>
          <Button
            variant="contained"
            size={isMobile ? "small" : "medium"}
            sx={{
              color: theme.palette.primary.main,
              background: theme.palette.common.white,
              gap: 1,
              boxShadow: "0px 1px 3px rgba(0, 0, 0, 0.1)",
              transition: "all 0.3s ease-in-out",
              ":hover": {
                background: theme.palette.common.white,
                transform: "translateY(-2px)",
                boxShadow: "0px 4px 12px rgba(0, 0, 0, 0.2)",
              },
            }}
            onClick={() => redirectToWhatsApp()}
          >
            <Icon fontSize={24} icon={"mdi:whatsapp"} />{" "}
            {Strings.button.chatWithUs}
          </Button>
        </Stack>
      )}
      {isMobile && (
        <IconButton onClick={() => toggleDrawer()}>
          <Icon
            icon={"material-symbols:menu-rounded"}
            color={theme.palette.tertiary.light}
            fontSize={32}
          />
        </IconButton>
      )}
      <Drawer anchor="top" open={open} onClose={() => toggleDrawer()}>
        <Stack gap={1} py={3} alignItems={"center"}>
          {defaultLinks.map((link, index) => (
            <Typography
              key={index}
              onClick={() => {
                link?.onClick();
                toggleDrawer();
              }}
              sx={{
                color: theme.palette.primary.main,
                fontSize: { sm: 13, md: 15 },
                cursor: "pointer",
                "&:hover": { textDecoration: "underline" },
              }}
            >
              {link?.label}
            </Typography>
          ))}
          <Button
            variant="contained"
            sx={{
              backgroundColor: theme.palette.primary.main,
              color: theme.palette.common.white,
              gap: 1,
              boxShadow: "0px 1px 3px rgba(0, 0, 0, 0.1)",
            }}
            onClick={() => redirectToWhatsApp()}
          >
            <Icon fontSize={24} icon={"mdi:whatsapp"} />{" "}
            {Strings.button.chatWithUs}
          </Button>
        </Stack>
      </Drawer>
    </Stack>
  );
};

export { NavBar };
