import React from "react";
import { Box, Paper, Typography, Stack, Avatar, Button } from "@mui/material";
import BuildIcon from "@mui/icons-material/Build";
import PrecisionManufacturingIcon from "@mui/icons-material/PrecisionManufacturing";
import HandymanIcon from "@mui/icons-material/Handyman";
import { useNavigate } from "react-router-dom";
import { PATH_DASHBOARD } from "@app/routes/paths";
import { Strings } from "@app/constants";

interface Service {
  title: string;
  description: string;
  Icon: React.ElementType;
}

const services: Service[] = [
  {
    title: "Welding Tools Supply",
    description: "High-quality tools and accessories for all welding needs.",
    Icon: BuildIcon,
  },
  {
    title: "Industrial Equipment",
    description: "Reliable and durable machinery for professional welders.",
    Icon: PrecisionManufacturingIcon,
  },
  {
    title: "Repair & Support",
    description:
      "Expert guidance and repair services to keep your work running.",
    Icon: HandymanIcon,
  },
];

export const ServicesSection = () => {
  const navigate = useNavigate();
  return (
    <Box sx={{ width: "100%", maxWidth: 1100, mx: "auto", py: 6 }}>
      <Stack
        direction="row"
        justifyContent="space-between"
        alignItems="center"
        sx={{ mb: 4, px: { xs: 2, sm: 0 } }}
      >
        <Typography variant="h4" fontWeight={700}>
          {Strings.headings.ourServices}
        </Typography>
        <Button
          variant="contained"
          onClick={() => navigate(PATH_DASHBOARD.service)}
        >
          {Strings.button.viewMore}
        </Button>
      </Stack>

      <Stack
        direction={{ xs: "column", sm: "row" }}
        spacing={3}
        justifyContent="center"
        alignItems="stretch"
      >
        {services.map((s, i) => (
          <Paper
            key={i}
            elevation={6}
            sx={{
              p: 4,
              flex: 1,
              borderRadius: 3,
              textAlign: "center",
              minHeight: 200,
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
              alignItems: "center",
              background: (theme) =>
                theme.palette.mode === "dark"
                  ? "rgba(30,41,59,0.85)"
                  : "rgba(255,255,255,0.9)",
              boxShadow: `0 8px 24px rgba(0,0,0,0.1)`,
              transition: "transform 200ms ease, box-shadow 200ms ease",
              "&:hover": {
                transform: "translateY(-6px)",
                boxShadow: `0 18px 40px rgba(15,23,42,0.14)`,
              },
            }}
          >
            <Avatar
              sx={{
                mb: 2,
                width: 72,
                height: 72,
                bgcolor: (theme) => theme.palette.primary.light,
                boxShadow: `0 6px 18px rgba(16,24,40,0.08)`,
              }}
            >
              <s.Icon style={{ fontSize: 36 }} />
            </Avatar>

            <Typography variant="h6" gutterBottom>
              {s.title}
            </Typography>
            <Typography variant="body2" color="text.secondary">
              {s.description}
            </Typography>
          </Paper>
        ))}
      </Stack>
    </Box>
  );
};
