import React from "react";

interface YouTubeShortsSliderProps {
  links: string[];
}

const YouTubeShortsSlider: React.FC<YouTubeShortsSliderProps> = ({ links }) => {
  const getVideoId = (url: string) => {
    const match = url.match(/\/shorts\/([a-zA-Z0-9_-]+)/);
    return match ? match[1] : null;
  };

  const displayLinks = links.slice(0, 5);

  return (
    <div
      style={{
        maxWidth: "100vw",
        margin: "0 auto",
        padding: "20px 5px",
        display: "flex",
        justifyContent: "center",
        gap: "15px",
        flexWrap: "wrap",
      }}
    >
      {displayLinks.map((link, idx) => {
        const videoId = getVideoId(link);
        if (!videoId) return null;

        return (
          <div
            key={idx}
            style={{
              flex: "1 1 220px", // responsive: shrink and grow
              maxWidth: "220px",
              minWidth: "100px",
              aspectRatio: "9/16",
              borderRadius: 12,
              overflow: "hidden",
              boxShadow: "0 4px 10px rgba(0,0,0,0.15)",
              backgroundColor: "#000",
            }}
          >
            <iframe
              src={`https://www.youtube.com/embed/${videoId}?autoplay=0&mute=0&controls=1&modestbranding=1`}
              style={{
                width: "100%",
                height: "100%",
                border: "none",
              }}
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
              allowFullScreen
              title={`YouTube Short ${idx}`}
            />
          </div>
        );
      })}
    </div>
  );
};

export { YouTubeShortsSlider };
