import React, { useEffect, useState } from "react";
import { Container, Typography, Stack, useTheme } from "@mui/material";
import { GedarTrader } from "@app/constants/gedarTrader";
import { Strings } from "@app/constants";
import JsonRenderer from "@app/components/JsonRenderer";
import { Loading } from "@app/components";

const TermAndCondition: React.FC = () => {
  const [data, setData] = useState<any | null>(null);
  const [loading, setLoading] = useState(true);
  const theme = useTheme();

  useEffect(() => {
    const fetchData = async () => {
      try {
        const res = await fetch(
          "https://raw.githubusercontent.com/gedartrader/Gedar-trader-web-server-json/refs/heads/main/termAndCondtions.json"
        );
        const jsonData = await res.json();
        setData(jsonData);
      } catch (e) {
        console.error("Error fetching privacy policy:", e);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  if (loading)
    return (
      <Stack
        sx={{
          height: 400,
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <Loading />
      </Stack>
    );

  return (
    <Container sx={{ py: { xs: 0, sm: 4 } }}>
      <Typography
        variant="h2"
        textAlign={"center"}
        sx={{
          mb: 4,
          textDecoration: "underline 2px solid",
          textDecorationColor: theme.palette.primary.main,
          textUnderlineOffset: "5px",
        }}
        gutterBottom
      >
        {Strings.headings.termAndCondition}
      </Typography>
      <JsonRenderer data={data} />
    </Container>
  );
};

export { TermAndCondition };
