import { Loading, ProductCard } from "@app/components";
import { Strings } from "@app/constants";
import { PATH_DASHBOARD } from "@app/routes/paths";
import { Stack, Typography, useTheme, Button } from "@mui/material";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

const Product = () => {
  const [data, setData] = useState<any | null>(null);
  const [loading, setLoading] = useState(true);
  const [selectedCategory, setSelectedCategory] = useState<string | null>(null);
  const theme = useTheme();
  const navigate = useNavigate();

  useEffect(() => {
    const fetchData = async () => {
      try {
        const res = await fetch(
          "https://raw.githubusercontent.com/gedartrader/Gedar-trader-web-server-json/refs/heads/main/products.json"
        );
        const jsonData = await res.json();
        setData(jsonData);
      } catch (e) {
        console.error("Error fetching products:", e);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  if (loading)
    return (
      <Stack
        sx={{
          height: 400,
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <Loading />
      </Stack>
    );

  // Extract unique categories
  const categories = Array.from(
    new Set(data?.products?.map((p: any) => p.category))
  );

  // Filter products by selected category
  const filteredProducts = selectedCategory
    ? data?.products?.filter((p: any) => p.category === selectedCategory)
    : data?.products;

  return (
    <Stack sx={{ mb: 5 }}>
      {/* Heading */}
      <Stack>
        <Typography
          variant="h2"
          textAlign={"center"}
          sx={{
            mb: 2,
            textDecoration: "underline 2px solid",
            textDecorationColor: theme.palette.primary.main,
            textUnderlineOffset: "5px",
          }}
          gutterBottom
        >
          {Strings.headings.products}
        </Typography>
      </Stack>

      {/* Category Filter */}
      <Stack
        direction="row"
        gap={1}
        sx={{
          overflowX: "auto",
          px: 2,
          py: 1,
          mb: 3,
        }}
      >
        <Button
          variant={selectedCategory === null ? "contained" : "outlined"}
          onClick={() => setSelectedCategory(null)}
          sx={{
            minWidth: 120,
            flexShrink: 0,
          }}
        >
          All
        </Button>
        {categories.map((cat: any) => (
          <Button
            key={cat}
            variant={selectedCategory === cat ? "contained" : "outlined"}
            onClick={() => setSelectedCategory(cat)}
            sx={{
              minWidth: 120,
              flexShrink: 0,
            }}
          >
            {cat}
          </Button>
        ))}
      </Stack>

      {/* Products */}
      <Stack
        sx={{
          width: "100%",
          flexDirection: "row",
          flexWrap: "wrap",
          alignItems: "center",
          justifyContent: "center",
          gap: 2,
        }}
      >
        {filteredProducts?.map((item: any) => (
          <ProductCard
            key={item.id}
            id={item.id}
            name={item.name}
            category={item.category}
            description={item.description}
            image={item.image}
            price={item.price}
            onViewMore={() =>
              navigate(`${PATH_DASHBOARD.productDetail}/${item.id}`)
            }
          />
        ))}
      </Stack>
    </Stack>
  );
};

export { Product };
