import { Loading, TestimonialsCarousel } from "@app/components";
import { imageSource, Strings } from "@app/constants";
import { useResponsive } from "@app/hooks";
import { PATH_DASHBOARD } from "@app/routes/paths";
import { redirectToWhatsApp } from "@app/utils/whatsapp";
import {
  Button,
  Stack,
  Typography,
  useMediaQuery,
  useTheme,
} from "@mui/material";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { ServicesSection } from "./service";
import WhatsAppIcon from "@mui/icons-material/WhatsApp";
import BuildIcon from "@mui/icons-material/Build";
import { ProductSlider } from "./ProductSlider";
import { YouTubeShortsSlider } from "./YoutubeShortsSlider";

const Home = () => {
  const theme = useTheme();
  const [data, setData] = useState<any | null>(null);
  // const isMobile = useResponsive("down", "md");
  const navigate = useNavigate();
  const [loading, setLoading] = useState(true);
  const [ytLinks, setYtLinks] = useState<string[]>([]);

  useEffect(() => {
    document.title = "Home | Gedar Trader";

    const fetchData = async () => {
      try {
        const res = await fetch(
          "https://raw.githubusercontent.com/gedartrader/Gedar-trader-web-server-json/refs/heads/main/products.json"
        );
        const jsonData = await res.json();
        setData(jsonData);
        const ytres = await fetch(
          "https://raw.githubusercontent.com/gedartrader/Gedar-trader-web-server-json/refs/heads/main/ytLinks.json"
        );
        const ytjsondata = await ytres.json();
        setYtLinks(ytjsondata);
      } catch (e) {
        console.error("Error fetching products:", e);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  const isMobile = useMediaQuery(theme.breakpoints.down("sm"));
  const isTablet = useMediaQuery(theme.breakpoints.between("sm", "md"));
  const isDesktop = useMediaQuery(theme.breakpoints.up("md"));

  // ✅ Decide slidesToShow based on screen size
  const slidesToShow = isMobile ? 1 : isTablet ? 2 : 4;

  if (loading)
    return (
      <Stack
        sx={{
          height: 400,
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <Loading />
      </Stack>
    );

  return (
    <Stack>
      {/* hero section */}
      <Stack
        sx={{
          width: "100%",
          height: "90vh",
          minHeight: 400,
          maxHeight: 500,
          flexDirection: "row",
          overflow: "hidden",
        }}
      >
        <Stack
          sx={{
            width: { sx: "100%", md: "60%" },
            height: "100%",
            px: 2,
          }}
        >
          <Stack
            sx={{
              width: { xs: "100%", sm: "80%" },
              height: "100%",
              justifyContent: "center",
              alignSelf: "center",
            }}
          >
            <Typography
              variant="h2"
              sx={{
                fontWeight: 700,
                lineHeight: 1.3,
                color: theme.palette.primary.main,
                fontSize: { xs: 24, sm: 36 },
              }}
            >
              {Strings.home.heroSection.fullHeading}
            </Typography>
            <Typography variant="body1">
              {Strings.home.heroSection.subHeading}
            </Typography>
            <Stack
              sx={{
                flexDirection: "row",
                gap: { xs: 1, sm: 5 },
                mt: 2,
              }}
            >
              <Button
                variant="contained"
                sx={{ width: { xs: 120, sm: 150 }, backgroundColor: "#128C7E" }}
                onClick={() => redirectToWhatsApp()}
                startIcon={<WhatsAppIcon />}
              >
                {Strings.button.whatsApp}
              </Button>
              <Button
                variant="contained"
                sx={{ width: { xs: 120, sm: 150 } }}
                startIcon={<BuildIcon />}
                onClick={() => navigate(PATH_DASHBOARD.service)}
              >
                {Strings.button.service}
              </Button>
            </Stack>
          </Stack>
        </Stack>
        {!isMobile && (
          <Stack
            sx={{
              flex: 1,
              alignItems: "self-end",
              justifyContent: "center",
            }}
          >
            <img
              src={imageSource.heroSection}
              style={{
                width: "100%",
                maxWidth: 700,
                aspectRatio: "1/1",
                background: "transparent",
                mixBlendMode: "multiply",
              }}
            />
          </Stack>
        )}
      </Stack>

      <YouTubeShortsSlider links={ytLinks} />

      {/* Product Slider Section */}
      {data && (
        <ProductSlider data={data.products} slidesToShow={slidesToShow} />
      )}
      {/* 👈 only 10 will show */}
      {/* ServicesSection */}
      <ServicesSection />
      {/* testimonial */}
      <TestimonialsCarousel />
    </Stack>
  );
};

export { Home };
