import { Stack, Typography, useTheme } from "@mui/material";
import { useNavigate } from "react-router-dom";

import { Strings } from "@app/constants";
import { PATH_DASHBOARD } from "@app/routes/paths";

const FooterLinks = () => {
  const theme = useTheme();
  const navigate = useNavigate();

  const linksData = [
    {
      name: Strings.footer.products,
      path: () => navigate(PATH_DASHBOARD.products),
    },
    {
      name: Strings.footer.aboutUs,
      path: () => navigate(PATH_DASHBOARD.aboutUs),
    },
    {
      name: Strings.footer.privacyPolicy,
      path: () => navigate(PATH_DASHBOARD.privacyPolicy),
    },
    {
      name: Strings.footer.termsAndCondition,
      path: () => navigate(PATH_DASHBOARD.termAndCondition),
    },
  ];

  return (
    <Stack
      gap={1}
      width={{ xs: "100%", sm: "30%" }}
      alignSelf={{ xs: "center", sm: "auto" }}
    >
      {linksData?.map((i) => (
        <Typography
          variant="subtitle2"
          fontWeight="500"
          sx={{ cursor: "pointer" }}
          color={theme.palette.common.white}
          fontSize={"14"}
          onClick={i.path}
          textAlign={{ xs: "center", sm: "start" }}
        >
          {i.name}
        </Typography>
      ))}
    </Stack>
  );
};

export { FooterLinks };
