import { Box, Stack, Typography, useTheme } from "@mui/material";
import { LoadingButton } from "@mui/lab";

import { FormProvider, RHFTextField } from "@app/components";
import { Strings, imageSource } from "@app/constants";
import { useResponsive } from "@app/hooks";

interface ContactUsFormProps {
  methods?: any;
  onSubmit?: (form: any) => void;
}

const ContactUsForm = ({ methods, onSubmit }: ContactUsFormProps) => {
  const theme = useTheme();
  const isMobile = useResponsive("down", "md");

  return (
    <>
      <Stack sx={{ width: "100%", backgroundColor: theme.palette.grey[100] }}>
        <FormProvider methods={methods}>
          <Typography
            textAlign="center"
            fontSize={28}
            fontWeight={700}
            sx={{
              mb: 38,
              textDecoration: "underline",
              textDecorationColor: theme.palette.primary.main,
            }}
          >
            {Strings.common.contactUs}
          </Typography>

          <Stack
            alignItems="center"
            sx={{
              backgroundColor: theme.palette.primary.main,
              height: "auto",
            }}
          >
            <Box
              sx={{
                backgroundColor: "white",
                borderRadius: 1,
                boxShadow: `0px 0px 2px ${theme.palette.primary.main}`,
                p: { xs: 3, sm: 4 },
                display: "flex",
                flexDirection: { xs: "column", md: "row" },
                alignItems: "center",
                gap: 4,
                width: { xs: "90%", md: "75%", lg: "65%" },
                mt: -35,
              }}
            >
              <Stack spacing={2} sx={{ width: { xs: "100%", md: "50%" } }}>
                <RHFTextField
                  name="name"
                  label="Your Name"
                  fullWidth
                  required
                />
                <RHFTextField
                  name="phone"
                  label="Your Phone Number"
                  fullWidth
                  required
                />
                <RHFTextField
                  name="message"
                  label="Your Message"
                  fullWidth
                  required
                  multiline
                  rows={4}
                  sx={{
                    backgroundColor: theme.palette.grey[50],
                    fontWeight: "400",
                    color: theme.palette.grey[600],
                    fontSize: 14,
                  }}
                />
                <LoadingButton
                  type="submit"
                  variant="contained"
                  fullWidth
                  sx={{
                    backgroundColor: theme.palette.primary.main,
                    color: theme.palette.common.white,
                    textTransform: "uppercase",
                    p: 1.5,
                  }}
                  onClick={methods.handleSubmit(onSubmit)}
                >
                  {Strings.footer.sendAMessage}
                </LoadingButton>
              </Stack>
              {!isMobile && (
                <Box
                  component="img"
                  src={imageSource.contactUs}
                  alt="contact"
                  sx={{
                    aspectRatio: "1/1",
                    width: { xs: "100%", md: "50%" },
                    borderRadius: 1,
                    objectFit: "cover",
                  }}
                />
              )}
            </Box>
          </Stack>
        </FormProvider>
      </Stack>
    </>
  );
};

export { ContactUsForm };
