import { Loading } from "@app/components";
import { Strings } from "@app/constants";
import { Stack, Typography, useTheme } from "@mui/material";
import { useEffect, useState } from "react";
import { ServicesCard } from "./serviceCard";

const Service = () => {
  const theme = useTheme();
  const [data, setData] = useState<any | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    document.title = "Service | Gedar Trader";
    const fetchData = async () => {
      try {
        const res = await fetch(
          "https://raw.githubusercontent.com/gedartrader/Gedar-trader-web-server-json/refs/heads/main/service.json"
        );
        const jsonData = await res.json();
        setData(jsonData);
      } catch (e) {
        console.error("Error fetching privacy policy:", e);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  if (loading)
    return (
      <Stack
        sx={{
          height: 400,
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <Loading />
      </Stack>
    );

  return (
    <Stack
      sx={{
        mb: 5,
      }}
    >
      <Stack>
        <Typography
          variant="h2"
          textAlign={"center"}
          sx={{
            mb: 4,
            textDecoration: "underline 2px solid",
            textDecorationColor: theme.palette.primary.main,
            textUnderlineOffset: "5px",
          }}
          gutterBottom
        >
          {Strings.headings.service}
        </Typography>
      </Stack>
      <ServicesCard data={data?.services} />
    </Stack>
  );
};

export { Service };
