import React from "react";
import {
  Stack,
  Card,
  CardMedia,
  CardContent,
  Typography,
  Button,
} from "@mui/material";
import { Strings } from "@app/constants";
import { redirectToWhatsApp } from "@app/utils/whatsapp";

interface Service {
  name: string;
  description: string;
  image: string;
}

interface ServicesProps {
  data: Service[];
}

const ServicesCard: React.FC<ServicesProps> = ({ data }) => {
  if (!Array.isArray(data)) return null;

  return (
    <Stack
      direction={{ xs: "column", sm: "row" }}
      gap={4}
      flexWrap="wrap"
      justifyContent="center"
      alignItems="flex-start"
      sx={{
        px: { xs: 1, sm: 0 },
      }}
    >
      {data.map((item, index) => (
        <Card
          key={index}
          sx={{
            width: { xs: "100%", sm: "45%", md: "30%" },
            boxShadow: 3,
            borderRadius: 2,
            display: "flex",
            flexDirection: "column",
          }}
        >
          {/* Service Image */}
          <CardMedia
            component="img"
            image={item.image}
            alt={item.name}
            sx={{ objectFit: "cover", height: { xs: 200, xl: 350 } }}
          />

          {/* Service Name & Description */}
          <CardContent sx={{ flexGrow: 1 }}>
            <Typography variant="h6" gutterBottom>
              {item.name}
            </Typography>
            <Typography variant="body2" color="text.secondary">
              {item.description}
            </Typography>
          </CardContent>

          {/* CTA Button */}
          <Stack sx={{ p: 2 }}>
            <Button
              variant="contained"
              color="primary"
              onClick={() =>
                redirectToWhatsApp(
                  `Hi Gedar Trader, I am interested in your service: "${item.name}". Please let me know the cost and details.`
                )
              }
            >
              {Strings.button.contactOnWhatsApp}
            </Button>
          </Stack>
        </Card>
      ))}
    </Stack>
  );
};

export { ServicesCard };
