import { GedarTrader } from "@app/constants/gedarTrader";

const redirectToWhatsApp = (message?: string) => {
  const phoneNumber = GedarTrader.phoneNumber;
  const baseUrl = "https://wa.me/";
  const url = message
    ? `${baseUrl}${phoneNumber}?text=${encodeURIComponent(message)}`
    : `${baseUrl}${phoneNumber}`;

  window.open(url, "_blank");
};

export { redirectToWhatsApp };
