import { imageSource, Strings } from "@app/constants";
import { Avatar, Box, Paper, Stack, Typography, useTheme } from "@mui/material";
import WorkspacePremiumIcon from "@mui/icons-material/WorkspacePremium";
import HandshakeIcon from "@mui/icons-material/Handshake";
import HistoryIcon from "@mui/icons-material/History";

const AboutUs = () => {
  const theme = useTheme();
  const features = [
    {
      id: "quality",
      title: "Top-quality Products",
      description: "High-grade welding tools and materials — built to last.",
      Icon: WorkspacePremiumIcon,
    },
    {
      id: "relationships",
      title: "Trusted Relationships",
      description: "Personalized service and support for every customer.",
      Icon: HandshakeIcon,
    },
    {
      id: "legacy",
      title: "Serving Since 1999",
      description: "A legacy of craftsmanship and trust for over two decades.",
      Icon: HistoryIcon,
    },
  ];
  return (
    <Stack>
      <Stack>
        <Typography
          variant="h2"
          textAlign={"center"}
          sx={{
            mb: 2,
            textDecoration: "underline 2px solid",
            textDecorationColor: theme.palette.primary.main,
            textUnderlineOffset: "5px",
          }}
          gutterBottom
        >
          {Strings.headings.aboutUs}
        </Typography>
        <Typography
          variant="body1"
          textAlign={"center"}
          sx={{
            mb: 4,
            color: theme.palette.grey[700],
          }}
        >
          {Strings.aboutUs.subHeading}
        </Typography>
      </Stack>
      <Stack
        sx={{
          width: { xs: "90%", md: "80%" },
          flexDirection: { xs: "column", md: "row" },
          alignItems: "center",
          justifyContent: "center",
          gap: { xs: 4, md: 10 },
          alignSelf: "center",
          mb: 3,
          py: 2,
          border: `2px solid ${theme.palette.primary.lighter}`,
          borderRadius: 2,
          background: theme.palette.common.white,
        }}
      >
        <Stack
          sx={{
            flex: 1,
            aspectRatio: "1/1",
            maxHeight: 400,
            alignSelf: "center",
          }}
        >
          <img
            src={imageSource.aboutUsImage}
            style={{ width: "100%", height: "100%" }}
          />
        </Stack>
        <Stack
          sx={{
            width: { xs: "100%", md: "50%" },
            py: 2,
            px: { xs: 2, md: 5 },
          }}
        >
          <Typography
            variant="h3"
            sx={{
              textDecoration: `1px underline solid ${theme.palette.primary.main}`,
              textUnderlineOffset: 6,
              fontWeight: 900,
            }}
          >
            Gedar Trader
          </Typography>
          <Typography
            variant="body1"
            sx={{ fontSize: 17, textAlign: "justify" }}
          >
            {Strings.aboutUs.para1}
          </Typography>
          <br />
          <Typography
            variant="body1"
            sx={{ fontSize: 17, textAlign: "justify" }}
          >
            {Strings.aboutUs.para2}
          </Typography>
          <br />
          <Typography
            variant="body1"
            sx={{ fontSize: 17, textAlign: "justify" }}
          >
            {Strings.aboutUs.para3}
          </Typography>
        </Stack>
      </Stack>
      <Typography
        variant="h2"
        textAlign={"center"}
        sx={{
          my: 2,
          textDecoration: "underline 2px solid",
          textDecorationColor: theme.palette.primary.main,
          textUnderlineOffset: "5px",
        }}
        gutterBottom
      >
        {Strings.aboutUs.ourMission}
      </Typography>
      <Stack
        sx={{ width: "100%", px: { xs: 2, sm: 4 }, py: { xs: 4, sm: 6 } }}
        spacing={3}
        justifyContent="center"
        alignItems="stretch"
        direction={{ xs: "column", sm: "row" }}
        flexWrap={"wrap"}
      >
        {features.map((f) => (
          <Paper
            key={f.id}
            elevation={6}
            sx={{
              flex: 1,
              p: 3,
              minHeight: 180,
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              textAlign: "center",
              borderRadius: 3,
              background: `linear-gradient(180deg, rgba(255,255,255,0.85) 0%, rgba(250,250,250,0.6) 100%)`,
              boxShadow: `0 6px 18px rgba(15,23,42,0.08)`,
              transition: "transform 200ms ease, box-shadow 200ms ease",
              "&:hover": {
                transform: "translateY(-6px)",
                boxShadow: `0 18px 40px rgba(15,23,42,0.14)`,
              },
            }}
          >
            <Avatar
              sx={{
                mb: 2,
                width: 72,
                height: 72,
                bgcolor: (theme) =>
                  theme.palette.mode === "dark"
                    ? "primary.dark"
                    : "primary.light",
                boxShadow: `0 6px 18px rgba(16,24,40,0.08)`,
              }}
            >
              <f.Icon
                style={{ fontSize: 36, color: theme.palette.common.white }}
              />
            </Avatar>

            <Typography variant="h6" component="h3" gutterBottom>
              {f.title}
            </Typography>

            <Typography variant="body2" color="text.secondary">
              {f.description}
            </Typography>
          </Paper>
        ))}
      </Stack>
      <Stack
        sx={{
          my: 3,
          alignItems: "center",
          justifyContent: "center",
          overflow: "hidden",
        }}
      >
        <iframe
          src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d864.6273677949151!2d73.88745135109015!3d29.907231762428843!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3917b5007d97497f%3A0x75d33e3d7606852f!2sGedar%20trader!5e0!3m2!1sen!2sin!4v1759171079127!5m2!1sen!2sin"
          width="80%"
          height="450"
          loading="lazy"
          style={{
            border: `2px solid ${theme.palette.primary.main}`,
            borderRadius: 20,
          }}
        ></iframe>
      </Stack>
    </Stack>
  );
};

export { AboutUs };
