import React from "react";
import { Stack, Typography, Button, Box, useTheme } from "@mui/material";
import { Strings } from "@app/constants";

interface ProductCardProps {
  id?: number;
  name: string;
  description: string;
  image?: string[]; // 🔹 image array directly
  price: number;
  category: string;
  onViewMore?: (id?: number) => void;
  isPrice?: boolean;
}

const ProductCard: React.FC<ProductCardProps> = ({
  id,
  name,
  description,
  image,
  price,
  category,
  onViewMore,
  isPrice = true,
}) => {
  const theme = useTheme();

  // 🔹 Default fallback image
  const defaultImage =
    "https://via.placeholder.com/300x200?text=No+Image+Available";

  // 🔹 Auto-pick first valid image or fallback
  const displayImage =
    image && image.length > 0 && image[0] ? image[0] : defaultImage;

  return (
    <Stack
      gap={2}
      sx={{
        width: 300,
        borderRadius: 2,
        overflow: "hidden",
        boxShadow: 5,
        backgroundColor: theme.palette.common.white,
      }}
    >
      {/* Image */}
      <Box
        component="img"
        src={displayImage}
        alt={name}
        sx={{
          width: "100%",
          height: 200,
          objectFit: "contain",
          borderTopLeftRadius: 12,
          borderTopRightRadius: 12,
        }}
        onClick={() => onViewMore?.(id)}
      />

      {/* Content */}
      <Stack spacing={1} sx={{ p: 2 }}>
        {/* Product Name */}
        <Typography
          variant="h6"
          sx={{
            fontWeight: 600,
            whiteSpace: "nowrap",
            overflow: "hidden",
            textOverflow: "ellipsis",
          }}
        >
          {name}
        </Typography>

        {/* Product Description */}
        <Typography
          variant="body2"
          color="text.secondary"
          sx={{
            display: "-webkit-box",
            WebkitLineClamp: 2,
            WebkitBoxOrient: "vertical",
            overflow: "hidden",
          }}
        >
          {description}
        </Typography>

        {/* Price */}
        {isPrice && (
          <Typography variant="subtitle1" sx={{ fontWeight: "bold", mt: 1 }}>
            ₹{price.toLocaleString("en-IN")}
          </Typography>
        )}

        {/* Category */}
        <Typography variant="caption" color="text.secondary">
          Category: {category}
        </Typography>

        {/* CTA */}
        <Button
          variant="contained"
          color="primary"
          sx={{ mt: 2, textTransform: "none" }}
          onClick={() => onViewMore?.(id)}
        >
          {Strings.button.viewMore}
        </Button>
      </Stack>
    </Stack>
  );
};

export { ProductCard };
